import React from 'react';
import Helmet from 'react-helmet';

import question1hard from '../../assets/images/question1hard.png';
import question2hard from '../../assets/images/question2hard.png';
import question3hard from '../../assets/images/question3hard.png';

import Layout from '../../components/layout';
import PageNavigation from '../../components/page-navigation';
import Lightbox from '../../components/lightbox';

import { makeStyles } from '@material-ui/core/styles';
import { Accordion, AccordionSummary, AccordionDetails, Typography } from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

const useStyles = makeStyles(theme => ({
  heading: {
    fontSize: theme.typography.pxToRem(16),
    fontWeight: theme.typography.fontWeightBold,
  },
  paragraph: {
    display: "block",
    textAlign: "left"
  },
  accordionArea: {
    marginBottom: theme.spacing(3)
  }
}));

const HintHard = () => {
  const classes = useStyles();

  return (
    <Layout>
      <section id="header">
        <div className="inner">
          <h1>ハードモードヒント・解答</h1>
        </div>
      </section>
      <Helmet title="ハードモードヒント・解答 | 氷蔵姉妹の挑戦" />
      
      <section id="one" className="main style1">
        <div className={`grid-wrapper ${classes.accordionArea}`}>
          <div className="col-12">
            <Accordion>
              <AccordionSummary expandIcon={<ExpandMoreIcon/>} className={classes.heading}>
                ハードモードにたどり着くには？
              </AccordionSummary>
              <AccordionDetails className={classes.paragraph}>
                <Typography>
                  あることをして、問題そのものを書き換えるんだ。<br/>
                  <br/>
                  ここで、問題を見直してみよう。<br/>
                  たとえば問題1は、「水色の問題」と書いてあって、だいたいの文字は水色で書かれている…<br/>
                  のだけど、「オルゴールを…言葉」の部分はオレンジ色だぞ！？<br/>
                  次に問題2を見てみると、緑色の問題のはずなのに、なぜか水色の文字が！<br/>
                  <br/>
                  そう。それぞれの問題から、違う色の文字(など)を追い出し、別の問題から自分の色の文字(など)を引っ張ってくることで、ハードモードの問題が完成するんだ。<br/>
                  それぞれの問題には背景にマス目が描いてあるので、同じ位置に移動させよう。                </Typography>
              </AccordionDetails>
            </Accordion>
            <Accordion>
              <AccordionSummary expandIcon={<ExpandMoreIcon/>} className={classes.heading}>
                問題1：水色の問題のヒント
              </AccordionSummary>
              <AccordionDetails className={classes.paragraph}>
                <Typography>
                  「水色の問題」なので、オレンジ色の「オルゴールを…言葉」の部分は不要だ。問題3に移動させよう。<br/>
                  そして、問題2にある水色の「・カタカナ」を持ってくる。<br/>
                  すると、問題1はこのように書き換わる。<br/>
                  <br/>
                  <Lightbox src={question1hard}/>
                  <br/>
                  カタカナを大きいものから小さいものへ繋いでいくことになった！<br/>
                  見てみると、左の絵にはカタカナが3つある。(わかりづらくて申し訳ないけど、「り」はひらがなだよ。)<br/>
                  これを大きさの順に繋いでいくと、ひらがな3文字の言葉ができるよ。<br/>
                  <br/>
                  「星を繋いで導かれる言葉」についてはノーマルモードと変わらない。<br/>
                  だから、答えは3+2で5文字の言葉になるよ！
                </Typography>
              </AccordionDetails>
            </Accordion>
            <Accordion>
              <AccordionSummary expandIcon={<ExpandMoreIcon/>} className={classes.heading}>
                問題2：緑色の問題のヒント
              </AccordionSummary>
              <AccordionDetails className={classes.paragraph}>
                <Typography>
                  「緑色の問題」なので、水色の「・カタカナ」は問題1にあげてしまおう。<br/>
                  そして、問題3から「かうに」と書かれた六角形の図形を貰ってくるんだ。<br/>
                  すると、問題2はこのように書き換わる。<br/>
                  <br/>
                  <Lightbox src={question2hard}/>
                  <br/>
                  3つの影は変わっていない。図1も変わっていないので、影に当てはまる図形はノーマルモードと同じはずだ。<br/>
                  しかし、問題3から持ってきた「かうに」があるために、図形の中の文字を横に読んだときの文章が変わる！<br/>
                  <br/>
                  文章は漢字に変換するとわかりやすくなる。<br/>
                  その条件に当てはまるところを見ると、漢字2文字が得られる。<br/>
                  その漢字を左から順に読むと、ひらがな4文字の言葉が得られるよ！
                </Typography>
              </AccordionDetails>
            </Accordion>
            <Accordion>
              <AccordionSummary expandIcon={<ExpandMoreIcon/>} className={classes.heading}>
                問題3：オレンジ色の問題のヒント
              </AccordionSummary>
              <AccordionDetails className={classes.paragraph}>
                <Typography>
                  「オレンジ色の問題」なので、緑色の「かうに」の六角形は問題2に移動させる。<br/>
                  そして問題1から「・オルゴールを繋いで導かれる言葉」を持ってくるんだ。<br/>
                  すると、問題3はこのように書き換わる。<br/>
                  <br/>
                  <Lightbox src={question3hard}/>
                  <br/>
                  AとBは変わらない。左の迷路にも変化はない。つまり、AとBに入る言葉は同じだ。<br/>
                  そこに「オルゴールを繋いで導かれる言葉」が加わったのだ。<br/>
                  しかし…オルゴールとは、問題1にあった絵だ。問題3にはない。どうやって解けというのか？<br/>
                  <br/>
                  落ち着いて問題文を見返してみると、「左の迷路の中からひらがなを集め」とある。<br/>
                  「オルゴールを繋いで導かれる言葉」も、迷路に関係しているはずだ。<br/>
                  迷路の中にオルゴールの絵なんてない。が…「おるごーる」はある！<br/>
                  オルゴールを「繋いで」とあるので、「おるごーる」を繋げてみよう。<br/>
                  そうすると、繋げた線はある形を示すよ！<br/>
                  その形に従うと5文字のひらがなを拾うことができる。<br/>
                  それらを並べ替えると、意味のある単語ができるよ！<br/>
                  <br/>
                  こうして、A、B、「オルゴールを繋いで導かれる言葉」の3つの言葉が集まった。<br/>
                  これらから連想される「形」とはなんだろう？<br/>
                  A、Bから連想される○○伝説に○○なんてでてこないじゃないか！<br/>
                  <br/>
                  ここも閃き力と知識の両方がいるところ。知識に関して、わからない場合はググってみてね。<br/>
                  そもそも、AとBはどこにいるんだっけ？<br/>
                  その、AとBがいる場所の近くを探して見ると、○○もいるはず。<br/>
                  そして、A、B、○○の3つと言えば！？
                </Typography>
              </AccordionDetails>
            </Accordion>
            <Accordion>
              <AccordionSummary expandIcon={<ExpandMoreIcon/>} className={classes.heading}>
                最終問題：紫色の問題のヒント
              </AccordionSummary>
              <AccordionDetails className={classes.paragraph}>
                <Typography>
                  ハードモードの問題1～3は解けた。<br/>
                  でも、その答えをTwitterユーザ「frig7nazo88」のツイートに当てはめても、意味のある言葉ができないぞ？<br/>
                  <br/>
                  そうそのとおり。<br/>
                  <br/>
                  ここで、「ハードモードにたどり着くには？」をもう一度思い出してほしい。<br/>
                  簡単に言うと「色をそろえるとハードモードの問題が完成するよ」ということだったね。<br/>
                  そしてそれは…最終問題も例外ではない！<br/>
                  最終問題から追い出す色はないけど、取り込む色は…<br/>
                  そういえば、他の問題に、場違いな色の文字があったような…
                </Typography>
              </AccordionDetails>
            </Accordion>
          </div>
        </div>
        <div className={`grid-wrapper ${classes.accordionArea}`}>
          <div className="col-12">
            <Accordion>
              <AccordionSummary expandIcon={<ExpandMoreIcon/>} className={classes.heading}>
                問題1：水色の問題の答え
              </AccordionSummary>
              <AccordionDetails className={classes.paragraph}>
                <Typography>
                  『ふたつおり』<br/>
                  <br/>
                  3つのカタカナ「ラ」「ジ」「オ」を大きさの通りこの順で繋げると、「ふたつ」(二つ)という単語が得られる。<br/>
                  星の方はノーマルモードから変わっていないので、「おり」(檻)。<br/>
                  この2つの単語をつなげて読んで、「ふたつおり」(二つ折り)が答えです。
                </Typography>
              </AccordionDetails>
            </Accordion>
            <Accordion>
              <AccordionSummary expandIcon={<ExpandMoreIcon/>} className={classes.heading}>
                問題2：緑色の問題の答え
              </AccordionSummary>
              <AccordionDetails className={classes.paragraph}>
                <Typography>
                  『あまなつ』<br/>
                  <br/>
                  影に当てはまる図形はノーマルモードと変わっていないので、同じように当てはめる。<br/>
                  しかし、その右に、問題3から持ってきた六角形の「かうに」があることで、文字を横に読むと<br/>
                  「答えはかめんのうえの上に」(答えは仮面の上の上に)<br/>
                  と文章が変わる。<br/>
                  図2には「仮」と「面」のマスがあるので、その上の上、つまり2マス上を見る。<br/>
                  左から読んで、「あまなつ」(甘夏)が答えです。
                </Typography>
              </AccordionDetails>
            </Accordion>
            <Accordion>
              <AccordionSummary expandIcon={<ExpandMoreIcon/>} className={classes.heading}>
                問題3：オレンジ色の問題の答え
              </AccordionSummary>
              <AccordionDetails className={classes.paragraph}>
                <Typography>
                  『ろげ』<br/>
                  <br/>
                  AとBはノーマルモードから変わっていないので、「おりひめ」(織姫)と「ひこぼし」(彦星)のまま。<br/>
                  「・オルゴールを繋いで導かれる言葉」とは、迷路の中で、お→る→ご→ー→ると線でつなぐ(「る」は同じ文字を2回通る)と、矢印が現れる。<br/>
                  この矢印に沿って右上にたどっていくと、拾えるひらがなは「うょくはち」。<br/>
                  これを並べ替えると、「はくちょう」(白鳥)となる。<br/>
                  <br/>
                  織姫とはこと座のベガ、<br/>
                  彦星とはわし座のアルタイル、<br/>
                  そこに「白鳥」とくれば、はくちょう座のデネブが加わって、<br/>
                  「夏の大三角」ができる。<br/>
                  よって答えは、左から二番目の、細長い三角形です。
                </Typography>
              </AccordionDetails>
            </Accordion>
            <Accordion>
              <AccordionSummary expandIcon={<ExpandMoreIcon/>} className={classes.heading}>
                最終問題：紫色の問題の答え
              </AccordionSummary>
              <AccordionDetails className={classes.paragraph}>
                <div>
                  『ゆかた』<br/>
                  <br/>
                  最終問題も、1～3と同じように、色をそろえると問題文が変わる。<br/>
                  問題1にある紫色の「s」、これを最終問題に持ってくると、探すべきTwitterユーザが<br/>
                  「sfrig7nazo88」<br/>
                  に変わる。<br/>
                  <br/>
                  そこからの解き方はノーマルモードと同じで、<br/>
                  言葉の作り方の色のついた数字に各問題の答えを当てはめると、<br/><br/>
                  <ul>
                    <li>なつまつり</li>
                    <li>げた</li>
                    <li>おふろ</li>
                  </ul>
                  となる。<br/>
                  ここからうまく連想できなければ、それはキーワードの作り方が良くないということで申し訳ないのですが、<br/>
                  連想される答えは「ゆかた」(浴衣)です。
                </div>
              </AccordionDetails>
            </Accordion>
          </div>
        </div>
        <div className="grid-wrapper">
          <div className="col-12">
            <PageNavigation prev="/question/" />
          </div>
        </div>
      </section>
    </Layout>
  );
};

export default HintHard;
